import React, { Component } from 'react'
import Header from '../../components/Header'
import './index.css'
import Carousel from './components/carousel'
import Topic from '../../components/TopicItem/topicItem'
// import { Link } from 'react-router-dom'
import Aside from '../../components/Aside'
import { homeTopicList ,topicCarousel} from '../../servers/index'
import { message } from 'antd'

const PAGE_SIZE = 10


export default class Index extends Component {
    constructor() {
        super()
        this.state = {
            list: [],
            carouselList:[],
            page: 1,
            has_next: false,
            isDataArrive: true
        }
    }

    componentDidMount() {
        const page = this.state.page
        const page_size = PAGE_SIZE
        const page_num = page
        this.getArticleList(page_num, page_size)
        this.getTopicCarousel()
        window.addEventListener('scroll', this.loadMore);
    }

    componentWillUnmount(){
        window.removeEventListener('scroll',this.loadMore)
    }

    loadMore=()=>{
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
        let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
        if (scrollTop + clientHeight === scrollHeight) {
            this.getMoreArticleList()
        }
    }

    getArticleList(page_num, page_size) {
        const data = {
            page_num,
            page_size
        }
        homeTopicList(data).then(res => {
            if (res.code === 200) {
                const { has_next, next_number } = res.data.pageInfo
                this.setState({ list: res.data.topics, has_next, page: next_number })
            } else {
                message.error(res.error)
            }
        })
    }

    //加载更多
    getMoreArticleList() {
        const { isDataArrive, has_next, page } = this.state
        if (!isDataArrive || !has_next || page === 1) {
            return
        }
        this.setState({ isDataArrive: false })
        const data = {
            page_num: page,
            page_size: PAGE_SIZE
        }
     
        homeTopicList(data).then(res => {
            if (res.code === 200) {
                const { has_next, next_number } = res.data.pageInfo
                this.setState({
                    list: [...this.state.list, ...res.data.topics],
                    has_next, page: next_number, isDataArrive: true
                })
            } else {
                message.error(res.error)
            }
        })

    }

    getTopicCarousel(){
        topicCarousel().then(res=>{
            if(res.code===200){
                this.setState({carouselList:res.data.topics})
            }
        })
    }



    render() {
        const { list ,carouselList} = this.state
        return (
            <div>
                <Header {...this.props} />
                <div className="container" style={{ marginTop: '30px' }}>
                    <div className="row">
                        <div className="col-md-8">
                            <Carousel data={carouselList} {...this.props}/>
                            <div style={{ marginTop: '30px' }}>
                                {list.map((item, index) => {
                                    return (
                                        <Topic data={item} key={index} {...this.props} />
                                    )
                                })}

                            </div>

                        </div>

                        <div className="col-md-4">
                            <Aside {...this.props}/>
                        </div>
                    </div>
                </div>



            </div>
        )
    }
}