import React, { Component } from 'react'
import { Carousel } from 'antd';
// import default_img from '../../../assets/imgs/banner01.jpg'
import default_img_2 from '../../../assets/imgs/banner02.jpg'

function entityToString(entity) {
    var div = document.createElement('div');
    div.innerHTML = entity;
    var res = div.innerText || div.textContent;
    return res;
}

export default class carousel extends Component {

    render() {
        const data = this.props.data
        return (
            <Carousel  autoplay>
                {data.map((item, index) => {
                    const images = item.images.map(img => {
                        return entityToString(img)
                    })
                    return (
                        <div className="carousel-item" key={item.id}
                            onClick={() => this.props.history.push(`/articleDetail/${item.username}/${item.id}`)}
                        >
                            <div className="carousel-title">{item.title}</div>
                            <img src={images[0] ? images[0] : default_img_2} alt="" className="figure-img img-fluid w-100" />

                        </div>
                    )
                })}
            </Carousel>
        )
    }

}